<template>
  <div class="article-list" style="width:1050px;">
    <blog-article/>
    <div>
      <!-- for문 반복 -->
      <div class="article-contents">
        <template  v-if="articleTagCatList.length > 0">
          <div v-for="articleTagCat in articleTagCatList" :key="articleTagCat.articleNo">
            <v-row class="article-post" style="justify-content:space-between">
              <div class="article-content" style="width:80%;">
                <p align="left" class="font-weight-medium caption mb-0" v-if="articleTagCat.artCategory">[{{articleTagCat.artCategory.categoryName}}]</p>
                <p align="left" class="font-weight-medium caption mb-0" v-if="articleTagCat.acategory">[<b>{{articleTagCat.acategory.acategorySource}}</b> {{articleTagCat.acategory.acategoryNumber}} - {{articleTagCat.acategory.title}}] </p>
                <h5 align="left" class="font-weight-black">
                  <!-- 일단 ArticleDetail.vue에 코드 잇어욥! -->
                  <router-link :to="`/bloghome/${$store.getters.userInfo.userId}/articledetail/${articleTagCat.article.articleNo}`" class="text-decoration-none text-dark">
                    {{articleTagCat.article.title}}
                  </router-link>
                </h5>
                <p align="left" class="font-weight-light body-1" v-html="articleTagCat.article.content">
                </p>
                <v-row class="article-bottom" style="justify-content:space-between">
                  <v-col class="article-info">
                    <p align="left" class="font-weight-light caption mb-0">
                      {{articleTagCat.article.writeDate}} · 댓글수 {{articleTagCat.article.likeCount}}
                      <v-icon small>mdi-comment-text-outline</v-icon>
                    </p>
                    <p align="left" class="font-weight-medium caption mb-0">
                      <span v-for="tag in articleTagCat.arttag" :key="tag.tagNo">
                        #{{tag.tagName}}
                      </span>
                      <span v-for="tag in articleTagCat.tmpTag" :key="tag.tmptagName">
                        #{{tag.tmptagName}}
                      </span>
                    </p>
                  </v-col>
                  <v-col class="article-icon" cols="2">
                    <v-btn icon color="grey" class="mx-0 px-0 mt-2">
                      <v-icon>mdi-star-outline</v-icon>
                    </v-btn>
                    <v-btn icon color="grey" class="mx-0 px-0 mt-2">
                      <v-icon>mdi-dots-horizontal</v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </div>
              <v-col>
                <v-img src="https://picsum.photos/200/200?random=2"></v-img>
              </v-col>
            </v-row>
            <hr />
          </div>
        </template>
        <v-col cols="12">
          <v-container class="max-width">
            <v-pagination
              v-model="page"
              :circle="circle"
              :disabled="disabled"
              :length="5"
              :next-icon="nextIcon"
              :prev-icon="prevIcon"
              :page="page"
              :total-visible="totalVisible"
            ></v-pagination>
          </v-container>
        </v-col>
      </div>
    </div>
  </div>
</template>

<script>
import BlogArticle from "./BlogArticle.vue";
import axios from 'axios';
export default {
    name: 'BoardDetail',
    data(){
        return {
            articleTagCatList: [],
            boardnum : this.$route.params.boardNo
        }
    },
    components:{
        BlogArticle
    },
    created(){
        axios.get(`/article/articles?boardNo=${this.boardnum}`)
        .then(({data}) => {
            this.articleTagCatList = data;
            console.dir(this.articleTagCatList);
            console.log(this.articleTagCatList.length);
        })
        .catch(() => {
            alert("게시판 글 목록을 가져오는 데 실패했습니다.");
        });
    },
    computed: {
    userInfo() {
        return this.$store.getters.boardnumInfo;
      },
    }
    
}
</script>

<style>

</style>