<template>
  <div id="article-create">
    <div class="article-setting" style="height:100%;">
      <v-row class="pl-3" style="justify-content:start;">        
        <h4 class="text-justify font-weight-bold m-0" style="vertical-align:middle;">
          <div style="height:10px;"></div>
          글쓰기
        </h4>
        <v-btn-toggle
          v-model="icon"
          borderless
        >
          <v-btn  @click="writeArticle" value="작성" color="white" class="text-decoration-none">
            <span class="hidden-sm-and-down mr-1">작성</span>
            <v-icon small>mdi-pencil</v-icon>
          </v-btn>
        </v-btn-toggle>
      </v-row>
      <br>

      <div class="article-setting-base" style="width:1050px;">
        <v-row class="pl-3">
          <v-col class="d-flex p-0 mr-2" cols="2">
            <v-select
              v-model="boardNo"

              :items="boardItems"
              item-value="boardNo" 
              item-text="boardName"
              label="게시판"
              outlined
              dense
            ></v-select>
          </v-col>
          <v-col class="d-flex p-0 mr-2" cols="2">
            <v-select
              v-model="article.openScope"

              :items="openItems"
              item-value="value" 
              item-text="name"
              label="공개"
              outlined
              dense
            ></v-select>
          </v-col>

          <span class="pt-1 mx-2">퍼가기 허용</span>
          <v-switch v-model="shareSwitch" class="mt-0"></v-switch>
          <span class="pt-1 mx-2">질문 요청</span>
          <v-switch v-model="questionSwitch" class="mt-0 mr-2"></v-switch>

          <v-col class="pt-0" cols="3" v-if="boardType == 'N'">
            <v-combobox
              v-model="catSelected"

              :items="categoryItems"
              item-value="categoryNo" 
              item-text="categoryName"
              label="카테고리를 선택해주세요."
              class="p-0"
            ></v-combobox>
          </v-col>
        </v-row>

        <v-row class="px-3" v-if="boardType == 'A'">
          <v-col class="d-flex p-0 mr-2" cols="2">
            <v-select
              v-model="siteSelected"

              :items="siteList"
              label="사이트"
              outlined
              dense
            ></v-select>
          </v-col>
          <v-col v-if="siteSelected != '기타'" class="p-0">
            <v-combobox
              v-model="problemSelected"

              :items="acategorys"
              item-value="acategoryNo"
              item-text="title"
              label="문제를 선택해주세요"
              class="p-0"
            >
              <template slot='selection' slot-scope='{ item }'>
                {{ item.acategoryNumber }}번 - {{ item.title }}
              </template>
              <template slot='item' slot-scope='{ item }'>
                {{ item.acategoryNumber }}번 - {{ item.title }}
              </template>
            </v-combobox>
          </v-col>
          <v-col class="p-0" v-else>
            <v-text-field 
              outlined
              clearable
              dense
              placeholder="문제 사이트"
              
              v-model="acategory.acategorySource"
            ></v-text-field>
            <v-text-field
              outlined
              clearable
              dense
              placeholder="문제 번호"

              v-model="acategory.acategoryNumber"
            ></v-text-field>

            <v-text-field
              outlined
              clearable
              dense
              placeholder="문제명을 입력해주세요"

              v-model="acategory.title"
            ></v-text-field>
            <v-text-field
              outlined
              clearable
              dense
              placeholder="문제링크를 입력해주세요"

              v-model="acategory.link"
            ></v-text-field>
          </v-col>
        </v-row>

        <v-row class="px-3">
          <div>
            <div style="height:10px;"></div>
            <span class="mr-3">제목</span>
          </div>
          <v-col class="p-0">
            <v-text-field
              v-model="article.title"
              outlined
              clearable
              dense
              placeholder="제목을 입력해주세요"
            ></v-text-field>
          </v-col>
        </v-row>


        <v-row class="pl-3">
          <div>
            <div style="height:3px;"></div>
            <span class="mr-3">태그 설정</span>
          </div>
          <v-col cols="11" class="p-0">
            <v-combobox
              v-model="tagSelected"
              
              :items="tagItems"
              item-value="tagNo" 
              item-text="tagName"
              label="태그를 선택해주세요. (입력후 enter)"
              multiple
              chips
              class="p-0 m-0"
            ></v-combobox>
          </v-col>
        </v-row>

        <ckeditor v-model="editorData" :config="editorConfig"></ckeditor>
        <!-- 글삭제 버튼[수정시에만] -->
        <div align="right" class="my-2 mx-0">
          <!-- 누르면 경고창!! -->
          <v-btn text color="error">글 삭제</v-btn>
        </div>

        <hr>
      </div>
    </div>
  </div>

</template>

<script>
import axios from 'axios';
export default {
  name: "BlogEditor",
  data() {
    return {
      article:{
        boardNo: '',
        title: '',
        content: '',
        openScope: '',
        thumbNail: '',
        articleType: '',
        sharable:'',
        reviewRequest: '',
      },
      boardNo:'',
      boardType:'',
      shareSwitch: true,
      questionSwitch: false,
      editorData: '<p>Content of the editor.</p>',
      editorConfig: {
          extraPlugins: 'prism',
					codeSnippet_theme: 'monokai_sublime',
      },
      // 이전 페이지의 게시판으로 초기값 설정
      boardItems: [
        ],
      openItems: [
        {
          value:'public',
          name:'전체공개'
        },
        {
          value:'protected',
          name:'맞팔공개'
        },
        {
          value:'private',
          name:'비공개'
        }
      ],
      siteList: [],
      siteSelected: "none",

      catSelected: '',
      tagSelected: [],
      problemSelected: '',

      
      acategory:{
	      acategorySource: '',			/*NN 알고리즘 문제 사이트*/
        acategoryNumber: '',			/*NN 알고리즘 문제 번호*/
	      title: '',						/*NN 알고리즘 문제명*/
	    	link: ''						/*사이트 링크*/
      },
      arttag: [],
      tmptag:[],

      tagItems: [],
      categoryItems: [ ],

      acategorys: [],
    };
  },
  watch:{
    siteSelected: function(){
      axios.get(`/article/acategorys?acategorySource=${this.siteSelected}`)
      .then( ({data}) => {
          this.acategorys = data;
      })
      .catch( () => {

      })
    },
    boardNo: function(){
      this.article.boardNo = this.boardNo;

      for(let board of this.boardItems){
        if(board.boardNo == this.boardNo){
          this.boardType = board.boardType;
          break;
        }
      }
    }
  },
  methods:{
    displayTitle(){
      alert("sssss");
    },
    writeArticle(){
      this.article.content = this.editorData;
      //추후 썸네일 요망
      // this.article.articleType = "N"; 서버의 서비스단에서 보드타입에 맞는 articleType삽입
      this.article.sharable = this.shareSwitch? 1 : 0;
      this.article.reviewRequest = this.reviewRequest? 1 : 0;
      
  
      if(this.boardType == 'A' && this.siteSelected != '기타' && ( !(this.problemSelected instanceof Object) || !this.problemSelected.acategoryNo ) ){
        alert("알고리즘 문제가 선택되지 않았습니다!");
        return;
      }      
      
      //태그 설정
      for(let tag of this.tagSelected){
        if(tag instanceof Object && tag.tagNo){
          this.arttag.push({tagNo : tag.tagNo});
        }else{
          this.tmptag.push({tmptagName : tag});
        }
      }



      console.log("==============")
      console.dir(this.arttag);
      console.dir(this.tmptag);

      if(this.siteSelected == '기타'){
        this.problemSelected = null;
      }
      
      axios.post("/article/CUD",{
        article: this.article,
        arttag: this.arttag,
        tmpTag: this.tmptag,
        artCategory: {category: this.catSelected.categoryNo},
        artAlgo: {acategoryNo: this.problemSelected.acategoryNo},
        acategory: this.acategory
      })
    }
  }
  ,
  created(){
    axios.get(`/blog/myboards?blogNo=${this.$store.getters.blogInfo.blogNo}`)
    .then(({data}) => {
      this.boardItems = data;
    })
    .catch(() => {
      alert("게시판 목록을 불러오는 데 실패했습니다.")
    })

    axios.get("/article/categorys")
    .then(({data}) => {
      this.categoryItems = data;
      console.dir(this.categoryItems)
    })
    .catch(() => {
      alert("카테고리 목록을 불러오는 데 실패했습니다.")
    })

    axios.get("/tag/tags")
    .then(({data}) => {
      this.tagItems = data;
      console.dir(this.tagItems)
    })
    .catch(() => {
      alert("태그 목록을 불러오는 데 실패했습니다.")
    })

    axios.get("/article/acategorys/site")
    .then(({data}) => {
      this.siteList = data;
      this.siteList.push('기타');
    })
    .catch(() =>{
      alert("사이트 목록을 가져오는 데 실패했습니다.")
    })
    
  },
  components: {}
};
</script>

<style></style>
