<template>
  <div class="blocklist container  col order">
    <div class="row py-3">
      <!-- data가 들어갈 부분 -->
      <div class="col" id="main">
        <v-container fluid>
          <v-row align="center">
            <v-col cols="2">
              <v-text-field v-model="search" clearable flat solo-inverted hide-details label="Search" @keyup.enter="select_blocklist"></v-text-field>
            </v-col>
            <b-button @click="select_blocklist">검색</b-button>
          </v-row>
        </v-container>
        <!-- 차단목록 -->
        <b-tabs content-class="mt-3">
          <b-tab title="차단목록" active>
            <v-card max-width="1100" class="mx-auto">
              <v-list-item v-for="user in blocklist" :key="user.number" two-line>
                <v-list-item-content>         
                  <v-list-item-title class="my-1">{{user.avatar}} {{user.nickName}}({{user.userId}})</v-list-item-title>
                </v-list-item-content>
                <button @click="block_delete(user.blocking)">차단해제</button>
               </v-list-item>
            </v-card>
          </b-tab>
        </b-tabs>


        <!-- 페이지네이션 -->
        <v-col cols="12">
          <v-container id="page" class="max-width">
            <v-pagination
              v-model="page"
              :circle="circle"
              :disabled="disabled"
              :length="10"
              :next-icon="nextIcon"
              :prev-icon="prevIcon"
              :page="page"
              :total-visible="totalVisible"
            ></v-pagination>
          </v-container>
        </v-col>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "blocklist",
  components: {},
  data() {
    return {
      rows: 100,
      blocklist : [],
      search : '',
      page: 1,
    };
  },
  created () {
    axios.get('/user/block').then((Response)=>{
      console.log(Response);
      this.blocklist = Response.data;
    })
  },
  methods : {
    select_blocklist() {
      axios.get('/user/block?searchValue=' + encodeURI(this.search)).then((Response)=>{
      console.log(Response);
      this.blocklist = Response.data;
    })
    },
    block_delete(number) {
      axios.delete('/user/block/' + number).then((Response)=>{
        console.log(Response);
        alert("차단해제되었습니다.");
        this.$router.go();
      })
    }
  }
};
</script>

<style></style>
