<template>
  <div id="BlogBase">
    <div v-if="this.blog_Info.blogNo!=null" v-cloak>
    <!-- 심심하면 현재게시판 색 표시 없애기 -->
    <!-- 프로필 점세개 버튼 추가해야함 -->
    <!-- 창줄여도 크기유지!! -->

    <!-- 블로그 내부 공통 (nav + sidebar) -->
    <!-- Nav -->
    <v-system-bar window dark style="height:50px;">
      <v-spacer></v-spacer>
      <!-- 버튼 기능추가해야함 -->
      <router-link :to="{ path: `/bloghome/${userInfo.userId}`}" style="text-decoration:none; color:#EEEE;">
        <span class="ml-3">내블로그</span>
      </router-link>
      <!-- 버튼효과 빼기 -->
      <div class="text-center">
        <v-menu
          v-model="menu"
          :close-on-content-click="false"
          :nudge-width="200"
          offset-x
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="black"
              dark
              v-bind="attrs"
              v-on="on"
              style="color:#EEEE;"
            >
              이웃블로그
            </v-btn>
          </template>

          <v-card>
            <div></div>
            <b class="p-3">이웃블로그</b>
            <v-list>
              <v-list-item>
                <v-list-item-avatar>
                  <img src="https://file3.instiz.net/data/file3/2018/06/21/b/4/0/b40633dd9df9e4ab4371b6a4f8801cdd.jpg" alt="John">
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title>jjiiiiinie</v-list-item-title>
                  <v-list-item-subtitle>jjiiiiinie의 행복한 세상</v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-action>
                  <v-btn
                    :class="fav ? 'red--text' : ''"
                    icon
                    @click="fav = !fav"
                  >
                    <v-icon>mdi-heart</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>

              <v-list-item>
                <v-list-item-avatar>
                  <img src="https://file3.instiz.net/data/file3/2018/06/21/b/4/0/b40633dd9df9e4ab4371b6a4f8801cdd.jpg" alt="John">
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title>ZeroZoo</v-list-item-title>
                  <v-list-item-subtitle>동물은없어요</v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-action>
                  <v-btn
                    :class="fav ? 'red--text' : ''"
                    icon
                    @click="fav = !fav"
                  >
                    <v-icon>mdi-heart</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
            </v-list>


            <v-card-actions>
              <v-spacer></v-spacer>

              <v-btn color="primary" text to="/" style="text-decoration:none;">전체보기</v-btn>
              <v-btn text @click="menu = false">취소</v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
      </div>

      <router-link to="/" style="text-decoration:none; color:#EEEE;">
        <span>블로그홈</span>
      </router-link>
      <!-- 수정 예정 : 김영주!!!!!!!!!! -->
      <v-icon class="ml-5">mdi-apps</v-icon>
    </v-system-bar>

    <v-row>
      <v-col
        v-for="(bar, i) in bars"
        :key="i"
        cols="12"
        class="pt-3 pb-6 px-3"
      >
        <v-row class="px-15" style="justify-content:space-between;">
          <v-col align="left">
            <!-- 버튼 기능 추가해야함 -->
            <router-link to="/" style="text-decoration:none; color:black;"><span class="mr-3">SSAZIT</span></router-link>
            <span class="mr-3">ㅣ</span>
            <router-link :to="{ path: `/bloghome/${userInfo.userId}`}" style="text-decoration:none; color:black;"><span>Blog</span></router-link>
          </v-col>
          <v-spacer></v-spacer>
          <!-- 블로그명 수정 [본인블로그일때만] -->
          <!-- 수정 버튼 누르면 확인 버튼으로 변신!!!!!! -->
          <v-btn-toggle
            v-model="icon"
            borderless
            v-if="userInfo.blogNo == searchInfo.blogNo"
          >
            <v-btn value="블로그명 수정" color="white" class="text-decoration-none" :to="{ path: `/bloghome/${userInfo.userId}/blogmanager`}">
              <span class="hidden-sm-and-down mr-1">수정</span>
              <v-icon small color="grey">mdi-pencil</v-icon>
            </v-btn>
          </v-btn-toggle>
        </v-row>

        <v-card color="white" flat>
          <v-toolbar :color="bar.class" :dark="bar.dark" prominent extended>
            <!-- 블로그 명 -->
            <!-- 현재 블로그로 보내주세요!!!!!! (내블로그x) -->
            <v-toolbar-title style="width:100%;">
              <router-link to="#" style="text-decoration:none; color:black;">
                <h1 style="font-weight: bold; color:grey;">{{searchInfo.blogName}}</h1>
              </router-link>
            </v-toolbar-title>
          </v-toolbar>
        </v-card>


      </v-col>
    </v-row>
    <hr>
    <div v-if="blog_Info.blogNo==0 || blog_Info.blogNo==null">
      마 블로그 없다 돌아가라!<br>
      <button type="button" @click="home">홈</button>
    </div>
    <div v-else>
      <v-app id="inspire">
        <div class="mx-15">
          <v-row>
            <v-card
              elevation="1"
            >
              <v-navigation-drawer
                permanent
              >
                <v-list flat>
                  <!-- 프로필 -->
                  <v-avatar color="orange" size="62" class="m-3 mb-1">
                    <v-img :src="userImage"></v-img>
                  </v-avatar>

                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>
                        <!-- 닉넴 주인 블로그로 이동!!!!!!!!!(내블로그x) -->
                        <router-link to="#" class="text-decoration-none text-dark">
                          {{ blogInfo.nickName }}({{blogInfo.userId}}) <br>
                          {{ blogInfo.blogName }}
                        </router-link>
                        <!-- 마페/정보(레벨, 쓴글?), 쪽지보기/보내기, 알림/팔로우 -->
                        <v-btn icon class="mx-0 px-0 mt-2">
                          <v-icon>mdi-dots-horizontal</v-icon>
                        </v-btn>
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
                

                <!-- <v-row style="width:100%;">
                  <v-col cols="2"></v-col>
                  <v-col cols="8" class="px-0">
                    <v-text-field
                      v-model=new_board
                      outlined
                      clearable
                      dense
                      placeholder="블로그 내 검색"
                    ></v-text-field>
                  </v-col>
                  <div>
                    <div style="height:3px;"></div>
                    <v-btn icon color="grey" class="mx-0 px-0 mt-2">
                      <v-icon>mdi-magnify</v-icon>
                    </v-btn>
                  </div>
                </v-row> -->

                <!-- 게시판 리스트 -->
                <v-list class="p-0">
                  
                  <v-divider
                    :key="index"
                    class="mb-0"
                  ></v-divider>
                  <v-subheader>
                    <p>게시판</p>
                  </v-subheader>
                  <p caption color="grey" class="font-weight-bold text-md-left pl-4 mb-0">전체보기</p>
                  <hr class="mt-0" style="border: 1px solid grey; width: 85%; text-align:center; margin: 0px auto;">
                  <!-- 이게 게시판 관리 게시판 영역-->
                    <!-- <div v-for="boardNum in boardsNo" :key="boardNum" style="text-align: left">
                  <div v-for="board in boards" :key="board.seqNo">
                    <p v-if="boardNum==board.groupNo && board.depth==0">{{board.boardName}}</p>
                    <p v-if="boardNum==board.groupNo && board.depth==1">-{{board.boardName}}</p>
                  </div>
                </div> -->
                <!-- boardsNo -->
                <!-- BoardItems -->
                    <div v-for="board in BoardItems" :key="board.seqNo">
                      <v-list-item v-if="board.seqNo==0" @click="boardnum(board.boardNo)" class="text-decoration-none">
                        <v-icon small class="mr-3">{{ articleIcon }}</v-icon>
                        <v-list-item-content>
                          <v-list-item-title class="text-md-left">{{ board.boardName }}</v-list-item-title><!-- Board-->
                        </v-list-item-content>
                      </v-list-item>
                      <v-list-item v-if="board.seqNo>0" @click="boardnum(board.boardNo)" class="text-decoration-none">
                        <v-list-item-content>
                          <v-list-item-title class="text-md-left" to="/">-{{ board.boardName }}</v-list-item-title><!-- Board-->
                        </v-list-item-content>
                      </v-list-item>
                    </div>

                  <!-- 갤러리 리스트 -->
                  <template v-for="(item, index) in GalleryItems">
                    <v-list-item
                      v-if="item.action"
                      :key="item.title"
                      :to="{ path: `/bloghome/${userInfo.userId}/bloggallery`}"
                      class="text-decoration-none"
                    >
                      <v-icon small class="mr-3">{{ bookIcon }}</v-icon>
                      <v-list-item-content>
                        <v-list-item-title class="text-md-left">{{ item.title }}</v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>

                    <!-- 구분선 -->
                    <v-divider
                      v-else-if="item.divider"
                      :key="index"
                      class="mb-0"
                    ></v-divider>

                    <!-- Gallery -->
                    <v-subheader
                      v-else-if="item.header"
                      :key="item.header"
                    >
                      {{ item.header }}
                    </v-subheader>
                  </template>
                </v-list>
                <hr>
                <!-- 게시판 관리 -->
                <v-btn-toggle
                  v-model="icon"
                  borderless
                  v-if="userInfo.blogNo == searchInfo.blogNo"
                >
                  <v-btn value="관리" :to="{ path: `/bloghome/${userInfo.userId}/boardmanager`}" color="white" class="text-decoration-none">
                    <v-icon small class="mr-1">mdi-wrench</v-icon>
                    <span class="hidden-sm-and-down">관리</span>
                  </v-btn>
                </v-btn-toggle>


                <!-- <div>{{ $route.name }}</div> -->

              </v-navigation-drawer>
            </v-card>
            <v-col>
              <router-view class="pl-10" style="display:block; align-items:center; width:100%">
              </router-view>
            </v-col>
          </v-row>
        </div>
      </v-app>
    </div>
  </div>
  <div v-else v-cloak>
    유효하지 않은 페이지입니다.
    <button @click="refresh">새로고침</button>
    <button @click="hoem">홈</button>
  </div>
</div>
</template>



<script>
import { 
  mdiCommentMultipleOutline, 
  mdiBookMultiple, 
  mdiDotsHorizontal,
  } from '@mdi/js';
import axios from 'axios'

export default {
  name: "BlogBase",
  data () {
    return { 
      fav: true,
      menu: false,
      message: false,
      hints: true,

      articleIcon: mdiCommentMultipleOutline,
      bookIcon: mdiBookMultiple,
      profileIcon: mdiDotsHorizontal,
      blogId :'',
      userImage: '',
      searchInfo:{}, //
      blogInfo:{}, // 해당 블로그 정보
      divider : true,
      header: 'board',
      boardsNo: '',
      profileItems: [
        { user: 'Nickname', },
      ],

      BoardItems: [
      ],

      GalleryItems: [
        { divider: true },
        { header: 'Gallery' },
        {
          action: 'label',
          title: '작품(23)',
          link: 'bloggallery',
        },
      ],
      bars: [
        { class: 'elevation-0' },
      ],
    }
  },
  components: {
  },
  created() {
    axios.get('/blog/searchbyuserid?userId=' + encodeURI(this.$route.params.userId))
    .then((Response)=>{
      console.log(this.$route.params.userId)
      console.log(Response.data + "어느 사람의 블로그인지");
      // this.userImage = this.serverURL + this.blog_Info.profileImg; 이게 문제임
      this.searchInfo = Response.data;
      this.$store.dispatch('Re_blogInfo',this.searchInfo)
      axios.get('/blog/boardlist?blogNo=' + Response.data.blogNo)
      .then((Res)=>{
        console.log(Res);
        this.BoardItems = Res.data;
        this.$store.dispatch('Re_boardInfo',Res.data)
      })
    }),
    axios.post('/user/list',{
      searchKey : "userId",
      searchValue : this.$route.params.userId
    }).then((Response)=>{
      console.log(Response);
      console.log("Hi");
      this.blogInfo = Response.data[0];
    })
    // console.log(this.searchInfo + "어느 사람의 블로그인지");
    console.log(this.blogInfo.blogNo + "블로그의 정보");
  },
  methods : {
    home() {
      this.$router.push('/');
    },
    boardnum(num) {
      this.$router.push('/bloghome/'+this.userInfo.userId+'/board/'+num);
      this.$router.go();
    },
    refresh() {
      this.$router.go();
    },
  },
  computed: {
    userInfo() {
      return this.$store.getters.userInfo;
    },
    blog_Info() {
      return this.$store.getters.blogInfo;
    },
    boardInfo() {
      return  this.$store.getters.boardInfo;
    },
    boardnumInfo() {
      return  this.$store.getters.boardnumInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
};
</script>
[v-cloak]::before {
  content: 'loading...'
}
[v-cloak] > * {
  display:none;
}


