<script>
import axios from 'axios';
export default {
  data() {
    return {
      article:{
        boardNo: '',
        title: '',
        content: '',
        openScope: '',
        thumbNail: '',
        articleType: '',
        sharable:'',
        reviewRequest: '',
      },
      openItems: [
        {
          value:'public',
          name:'전체공개'
        },
        {
          value:'protected',
          name:'맞팔공개'
        },
        {
          value:'private',
          name:'비공개'
        }
      ],
      catSelected: '',
      tagSelected: [],
      tagItems: [],
      categoryItems: [ ],
    }
  },
  method: {
    writeArticle(){
      this.article.content = this.editorData;
      //추후 썸네일 요망
      // this.article.articleType = "N"; 서버의 서비스단에서 보드타입에 맞는 articleType삽입
      this.article.sharable = this.shareSwitch? 1 : 0;
      this.article.reviewRequest = this.reviewRequest? 1 : 0;
      
  
      if(this.boardType == 'A' && this.siteSelected != '기타' && ( !(this.problemSelected instanceof Object) || !this.problemSelected.acategoryNo ) ){
        alert("알고리즘 문제가 선택되지 않았습니다!");
        return;
      }      
      
      //태그 설정
      for(let tag of this.tagSelected){
        if(tag instanceof Object && tag.tagNo){
          this.arttag.push({tagNo : tag.tagNo});
        }else{
          this.tmptag.push({tmptagName : tag});
        }
      }



      console.log("==============")
      console.dir(this.arttag);
      console.dir(this.tmptag);

      if(this.siteSelected == '기타'){
        this.problemSelected = null;
      }
      
      axios.post("/article/CUD",{
        article: this.article,
        arttag: this.arttag,
        tmpTag: this.tmptag,
        artCategory: {category: this.catSelected.categoryNo},
        artAlgo: {acategoryNo: this.problemSelected.acategoryNo},
        acategory: this.acategory
      })
    }
  }
}
</script>

