<template>
  <div class="messages container col order">
    <div class="row py-3">
      <!-- data가 들어갈 부분 -->
      <div class="col" id="main">
        <!-- 검색 -->
        <div id="right">
          <v-container>
            <v-row align="center">
              <v-col cols="2">
                <v-text-field v-model="search" clearable flat solo-inverted hide-details label="Search" @keyup.enter="search_message"></v-text-field>
              </v-col>
              <li><b-button @click="search_message">검색</b-button></li>
              <li><b-button type="button" @click="send_msg_to">쪽지보내기</b-button></li>
            </v-row>
          </v-container>
        </div>

        <!-- 탭 -->
        <b-tabs content-class="mt-3">
          <b-tab title="받은쪽지함" @click="trans_receive" active></b-tab>
          <b-tab title="보낸쪽지함" @click="trans_send"></b-tab>
        </b-tabs>

        <!-- 받은 쪽지함 -->
        <div v-if="tabs">
          <div class="col-3" id="left" v-for="message in receive_data" :key="message.nickName">
            <v-card>
            <!-- <v-card class="mx-auto" width="300" height="250"> -->
              <v-card-text id="card-text">
              <div v-if="message.read">읽음</div>
                <p align="left">from.{{message.nickName}}</p>
                <div class="text--primary">
                {{message.content}}
                </div>
              </v-card-text>
              <v-card-text id="card-text2">
                <p>{{message.sendDate}}</p>
              </v-card-text>
              <v-card-actions>
                <v-btn text color="deep-purple accent-4" @click="repeat_message(message)">답장</v-btn>
                <v-btn text color="deep-purple accent-4" @click="read_message(message)">읽음</v-btn>
                <v-btn text color="deep-purple  accent-4" @click="delete_message(message)">삭제</v-btn>
              </v-card-actions>
            </v-card>
          </div>
          <modal v-if="showModal" @close="showModal = false">
            <transition name="modal">
              <div class="modal-mask">
                <div class="modal-wrapper">
                
                    <div id="app">
                      <v-container fluid>
                        <v-row>
                          <v-col cols="12" md="8">
                            <v-sheet elevation="12" class="pa-12">
                              <v-text-field
                                v-model="model"
                                :label="label"
                                :hint="hint"
                                :placeholder="placeholder"
                                :outlined="outlined"
                                :solo="solo"
                                :persistent-hint="persistentHint"
                                :counter="counterEn ? counter : false"
                              ></v-text-field>
                              <button class="modal-default-button" @click="send_message()">
                              전송
                              </button>
                              <button class="modal-default-button" @click="cancel()">
                              취소
                              </button>
                            </v-sheet>
                          </v-col>
                        </v-row>
                      </v-container>
                    </div>
                </div>
              </div>
            </transition>
          </modal>  
        </div>

          <!-- 보낸 쪽지함 -->
          <div v-if="!tabs">
            <div class="col-3" id="left" v-for="message in send_data" :key="message.userName">
              <v-card class="mx-auto" max-width="300" max-height="300">
                <v-card-text id="card-text">
                  <p align="left">to.{{message.nickName}}</p>
                  <div class="text--primary">
                  {{message.content}}
                  </div>
                </v-card-text>
                <v-card-text id="card-text2">
                  <p>{{message.sendDate}}</p>
                </v-card-text>
                <v-card-actions id="btn">
                  <v-btn text color="deep-purple accent-4" @click="delete_message(message)">삭제</v-btn>
                </v-card-actions>
              </v-card>
            </div>
          </div>

          <modal v-if="showDelete" @close="showDelete = false">
            <transition name="modal">
              <div class="modal-mask">
                <div class="modal-wrapper">
                
                    <div id="app">
                      <v-container fluid>
                        <v-row>
                          <v-col cols="12" md="5">
                            <v-sheet elevation="12" class="pa-12">
                              해당 쪽지를 삭제하시겠습니까?
                              <button class="modal-default-button" @click="canceldelete()">
                              취소
                              </button>
                              <button class="modal-default-button" @click="delete_message_ok()">
                              삭제
                              </button>
                            </v-sheet>
                          </v-col>
                        </v-row>
                      </v-container>
                    </div>
                </div>
              </div>
            </transition>
          </modal>  

          <!-- 쪽지 보내기 -->
          <modal v-if="showSend" @close="showSend = false">
            <transition name="modal">
              <div class="modal-mask">
                <div class="modal-wrapper">
                
                  <div id="app">
                    <v-container fluid>
                      <v-row>
                        <v-col cols="12" md="8">
                          <v-sheet elevation="12" class="pa-12">
                          <v-text-field v-model="model_to" placeholder="상대방ID를 입력하시오."></v-text-field>
                            <v-text-field
                              v-model="model"
                              label="메세지"
                              :hint="model_to+'님에게'"
                              :placeholder="placeholder"
                              :outlined="outlined"
                              :solo="solo"
                              :persistent-hint="persistentHint"
                              :counter="counterEn ? counter : false"
                            ></v-text-field>
                            <button class="modal-default-button" @click="get_to_data()">
                            전송
                            </button>
                            <button class="modal-default-button" @click="cancel_send()">
                            취소
                            </button>
                          </v-sheet>
                        </v-col>
                      </v-row>
                    </v-container>
                  </div>
                </div>
              </div>
            </transition>
          </modal>  
      </div>
    </div>

    <!-- 페이지네이션 -->
    <v-col cols="12">
      <v-container id="page" class="max-width">
        <v-pagination
          v-model="page"
          :circle="circle"
          :disabled="disabled"
          :length="5"
          :next-icon="nextIcon"
          :prev-icon="prevIcon"
          :page="page"
          :total-visible="totalVisible"
        ></v-pagination>
      </v-container>
    </v-col>
  </div>
</template>

<script>
import axios from 'axios'
// import ReMessage from '@/views/Message/ReMessage.vue'
export default {
  name: "messages",
    data() {
      return{
        items1: ['전체선택', '보낸사람', '내용'],
        items2: ['유저ID', '유저이름', '닉네임'],
        receive_user: 'From.',
        receive_message: '안녕하세요 맞팔해요~',
        receive_data:[],
        send_data:[],
        tabs : true,
        showModal: false,
        showDelete: false,
        showSend: false,
        model_to : '',
        model: '',
        label: '답글',
        hint: '누구에게',
        placeholder: '내용을 입력하시오.',
        outlined: true,
        persistentHint: true,
        counterEn: true,
        counter: 90,
        my_index:'',
        your_index:'',
        msg_index:'',
        your_data : {},
        search : "",
        page: 1,
      }
    },
    created(){
        axios.get('/user/rcvmsg').then((Response)=>{
          this.$store.dispatch('receive_message',Response.data);
          this.receive_data = this.$store.getters.receive_message;
        }),
        axios.get('/user/sendmsg').then((Response)=>{
          this.$store.dispatch('send_message',Response.data);
          this.send_data = this.$store.getters.send_message;
        })
    },
    computed : {
      receive_vmessage() {
        return this.$store.getters.receive_message;
      },
      send_vmessage() {
        return this.$store.getters.send_message;
      }
    },
    methods: {  
      trans_receive() {
        this.tabs=true;
        axios.get('/user/rcvmsg').then((Response)=>{
          this.$store.dispatch('receive_message',Response.data);
          this.receive_data = this.$store.getters.receive_message;
          this.search="";
        })
      },
      trans_send() {
        this.tabs=false;
        axios.get('/user/sendmsg').then((Response)=>{
          this.$store.dispatch('send_message',Response.data);
          this.send_data = this.$store.getters.send_message;
          this.search="";
        })
      },
      repeat_message(data){
        this.showModal = true
        this.hint = data.nickName+"에게"
        this.my_index = data.messageTo
        this.your_index = data.messageFrom
      },
      read_message(data){
        axios.put(`/user/readmsg/${data.messageNo}`)
        .then((Response)=>{
          console.log(Response);
          this.$router.go()
        })
      },
      delete_message(data) {
        this.showDelete = true
        this.msg_index = data.messageNo
      },
      cancel() {
        this.showModal = false;
        this.model="";
      },
      canceldelete() {
        this.showDelete = false;
      },
      cancel_send() {
        this.showSend = false;
      },
      send_message(){
        this.showModal = false;
        axios.post('/user/sendmsg',{
          content : this.model,
          messageTo : this.your_index
        }).then((Response)=>{
          console.log(Response);
          alert("전송되었습니다.");
          this.$router.go()
        }).catch((error)=>{
          console.log(error);
          alert("전송에 실패했습니다.");
        })
      },
      delete_message_ok(){
        this.showDelete = false;
        if(this.tabs){
          axios.delete(`/user/rcvmsg/${this.msg_index}`)
          .then((Response)=>{
            console.log(Response);
            alert("삭제되었습니다.");
            this.$router.go()
          }).catch((error)=>{
            console.log(error);
            alert("에러 발생");
          })
        }else{
          axios.delete(`/user/sendmsg/${this.msg_index}`)
          .then((Response)=>{
            console.log(Response);
            alert("삭제되었습니다.");
            this.$router.go()
          }).catch((error)=>{
            console.log(error);
            alert("에러 발생");
          })
        }
      },
      send_msg_to(data) {
        this.showSend = true
        this.hint = data.nickName+"에게"
        this.my_index = data.messageTo
        this.your_index = data.messageFrom
      },
      get_to_data(){
        axios.post('/user/list',{
            searchKey : 'userId',
            searchValue : this.model_to
          }).then((Response)=>{
            console.log(Response);
            this.your_index = Response.data[0].userNo;
            this.send_message();
            this.showSend=false;
          })
      },
      search_message() {
        if(this.tabs){
        axios.get('/user/rcvmsg?searchValue=' + encodeURI(this.search))
        .then((Response)=>{
          this.receive_data = Response.data;
        })}
        else{
          axios.get('/user/sendmsg?searchValue=' + encodeURI(this.search))
          .then((Response)=>{
          this.send_data = Response.data;
        })
        }
      }

    },
  components: {}
};
</script>

<style>
.container {
  margin: 0px 10px;
  width: 100%;  
  text-align: center;
}
#left {
  float: left;
  width: 100%;
}
#right {
  float: right;
  width: 100%;
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-default-button {
  float: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

#main {
  padding: 0 0px;
}
#card-text {
  height: 180px;
  text-align: center;
}
#card-text2 {
  padding: 0 15px;
  text-align: right;
}
#page {
  text-align: center;
  margin: 0;
  width: 100%;
}
</style>
