<template>
  <!-- 전체적으로 디자인만 해둔 상태니깐 수정 부탁합니당!!!! -->
  <div class="container p-0" style="height:100%;" v-if="searchInfo.userNo==userInfo.userNo">
    <v-row class="pl-3" style="justify-content:start;">        
      <h4 class="text-justify font-weight-bold m-0" style="vertical-align:middle;">
        <div style="height:10px;"></div>
        게시판 관리
      </h4>
      <v-btn-toggle
        v-model="icon"
        borderless
      >
        <!--<v-btn value="작성" color="white" class="text-decoration-none" @click="modify_blog">
          <span class="hidden-sm-and-down mr-1">수정</span>
          <v-icon small>mdi-checkbox-marked-outline</v-icon>
        </v-btn> -->
      </v-btn-toggle>
    </v-row>
    <br>
    <v-row class="pl-0" justify="start" style="width:1050px;">
      <v-col cols="5">
        <div class="text-center mb-3">
          <v-btn class="mr-3" small tile outlined color="grey" @click="create_board">
            <v-icon left>mdi-plus</v-icon>게시판 추가
          </v-btn>
          <v-btn class="mr-3" small tile outlined color="grey">
            <v-icon left>mdi-plus</v-icon>구분선 추가
          </v-btn>
          <v-btn class="ml-15" small tile outlined color="grey" @click="delete_board">
            <v-icon left>mdi-minus</v-icon>삭제
          </v-btn>
        </div>
        <div class="sortable" style="height:300px; border: 1px solid grey">
          <div class="sortable" style="border: 1px solid grey">SORTABLE</div>
              <div v-for="(board,index) in boards" :key="board.seqNo" style="text-align: left">  <!-- 이게 게시판 관리 게시판 영역-->
                  <p v-if="board.seqNo==0" @click="select_board(board,index)"><v-icon small class="mr-3">{{ articleIcon }}</v-icon><button type="button">{{board.boardName}}</button> </p>
                  <p v-if="board.seqNo!=0" @click="select_board(board,index)"><button type="button" @click="select_board(board,index)">-{{board.boardName}}</button> </p>
              </div>
        </div>
      </v-col>

      <v-col cols="7" class="px-10">
        <div style="height:45px;"></div>
        <v-row>
          <p class="mb-6 mr-3 " style="align-items:center; display:flex;">게시판명</p>
          <v-text-field
            v-model=new_board
            outlined
            clearable
            dense
            placeholder="게시판명을 입력해주세요"
          ></v-text-field>
        </v-row>

        <v-row>
          <p class="mb-6 mr-3 " style="align-items:center; display:flex;">게시판타입</p>
          <v-select
            :items="sortItems"
            v-model="type_value"
            placeholder="게시판 타입을 선택해주세요"
            outlined
            dense
          ></v-select>
        </v-row>

        <v-row>
          <p class="mb-6 mr-3 " style="align-items:center; display:flex;">게시판정렬</p>
          <v-btn class="mr-3" small tile outlined color="grey" @click="up_board">
            <v-icon left>mdi-chevron-up</v-icon>위
          </v-btn>
          <v-btn class="mr-3" small tile outlined color="grey" @click="down_board">
            <v-icon left>mdi-chevron-down</v-icon>아래
          </v-btn>
           <v-btn class="mr-3" small tile outlined color="grey" @click="left_board">
            <v-icon left>mdi-chevron-left</v-icon>좌
          </v-btn>
          <v-btn class="mr-3" small tile outlined color="grey" @click="right_board">
            <v-icon left>mdi-chevron-right</v-icon>우
          </v-btn>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
// blogmanager.vue 도 정리해주세요
import { 
  mdiCommentMultipleOutline, 
  } from '@mdi/js';
import axios from "axios"
export default {
  name: "BlogManager",
  data() {
    return {
      articleIcon: mdiCommentMultipleOutline,
      typeselected: "none",
      blogname: '',
      searchInfo:'',
      myInfo:'',
      updateInfo:'',
      boards: '',
      boardsNo: '',
      new_board: '',
      type_value: '',
      select_bd_num : '',
      beforeboard:{depth : -1},
      nowboard:{depth : -1},
      afterboard:{depth : -1},
      beforeboard_index:'',
      nowboard_index:'',
      afterboard_index:'',
      heads:{},
      temp_change_before : {},
      temp_change_now : {},
      temp_change_after : {},
      current_index : '',
      temp_data: {},
      now_board_count: '',
      temp_boards:[],
      nameRules: [
        v => !!v || '블로그명을 입력해주세요.',
        v => v.length <= 20 || '20자 이내로 입력해주세요.',
      ],
      sortItems: ['자유', '알고리즘'],
    };
  },
  components: {},
  created() {
    axios.get('/blog/searchbyuserid?userId=' + encodeURI(this.$route.params.userId))
    .then((Response)=>{
      console.log(Response.data+"2");
      this.blogname = Response.data.blogName;
      this.$store.dispatch('Re_blogInfo',Response.data)
      this.searchInfo = Response.data;
      // axios.get('/blog/myboards?blogNo='+ Response.data.blogNo)
      // .then((Res)=>{
      //   console.log(Res);
      //   this.boards = Res.data;
      //   this.$store.dispatch('Re_boardInfo',Response.data)
      // }),
      axios.get('blog/gndist?blogNo=' + this.searchInfo.blogNo).then((num)=>{
        this.heads = num.data;
        console.log(this.heads.length);
        console.log(this.heads.length+"길이");
        axios.get('/blog/boardlist?blogNo=' + Response.data.blogNo)
        .then((Res)=>{
          console.log(Res+"3");
          // for(var i=0;i<Res.data.length;i++){
            
          // }
          this.boards = Res.data;
          this.$store.dispatch('Re_boardInfo',Res.data)
        })
      })
    })
  },
  computed: {
    userInfo() {
      return  this.$store.getters.userInfo;
    },
    boardInfo() {
      // console.log(this.$store.getters.boardInfo);
      console.log("보드 갱신");
      return  this.$store.getters.boardInfo;
    },
  },
  methods : {
    home() {
      this.$router.push('/');
    },
    delete_blog() {
      axios.delete(`/blog/CUD?blogNo=`+this.searchInfo.blogNo)
      .then((Response)=>{
        console.log(Response);
        alert("블로그가 삭제되었습니다.");
        axios.get('/user/mypage').then((Response)=>{
                this.$store.dispatch('Re_userInfo',Response.data)
            })
        this.$router.push("/");
      })
    },
    modify_blog() {
      axios.put(`/blog/CUD?blogNo=`+this.userInfo.blogNo+`&blogName=`+ encodeURI(this.blogname)+`&userNo=`+this.userInfo.userNo)
      .then((Response)=>{
        alert("블로그가 수정되었습니다.");
        console.log(Response);
              axios.get('/user/mypage').then((Response)=>{  //이건 유저 정보 갱신
                this.$store.dispatch('Re_userInfo',Response.data)
            }),
            axios.get('/blog/searchbyuserid?userId=' + encodeURI(this.$route.params.userId)) //이건 블로그 정보 갱신
            .then((Response)=>{
              console.log(Response.data);
              this.$store.dispatch('Re_blogInfo',Response.data)
              this.searchInfo = Response.data;
            })
        this.$router.push({ path: `/bloghome/${this.userInfo.userId}`});
      }).catch((error)=>{
        alert("에러");
        console.log(error);
      })
    },
    create_board() {
         let temp_depth = 1;
      if(this.boards.length==0){
        temp_depth = 1;
      }else{
        temp_depth = this.boards[this.boards.length-1].depth + 1;
      }
      if(this.type_value=='자유'){
        console.log("");
        axios.post('/blog/myboard/CUD?boardName=' + encodeURI(this.new_board) + '&blogNo=' + this.searchInfo.blogNo + '&boardType=N&depth=' + temp_depth
        ).then((Response)=>{
          console.log(Response);
          alert("게시판이 추가되었습니다.");
          this.$router.go();
        }).catch((error)=>{
          alert(error);
        })
      }else if(this.type_value=='알고리즘'){
        console.log("");
        axios.post('/blog/myboard/CUD?boardName=' + encodeURI(this.new_board) + '&blogNo=' + this.searchInfo.blogNo + '&boardType=A&depth=' + temp_depth
        ).then((Response)=>{
          console.log(Response);
          alert("게시판이 추가되었습니다.");
          this.$router.go();
        }).catch((error)=>{
          alert(error);
        })
      }
    },
    select_board(data,index) {
      this.select_bd_num = data.boardNo;
      this.nowboard = data;
      this.current_index = index;
      if(index!=0){
        this.beforeboard = this.boards[index-1];
      }
      if(index==0){
        this.beforeboard = {depth : -1};
      }

      if(index!=(this.boards.length-1)){
        this.afterboard = this.boards[index+1];
      }
      if(index==(this.boards.length-1)){
        this.afterboard = {depth : -1};
      }
      console.log("이전 : " + this.beforeboard.boardName);
      console.log("현재 : " + this.nowboard.boardNo);
      console.log("다음 : " + this.afterboard.boardName);
      // console.log("인덱스 : " + index);
      // if(index!=0){
      // console.log("전값 : " + this.boards[index-1].boardName);
      // }
      // console.log("현값 : " + this.boards[index].boardName);
      // if(index!=(this.boards.length-1)){
      // console.log("후값 : " + this.boards[index+1].boardName);
      // }
    },
    delete_board() {
        this.temp_now_count = 0;
        for(let i=0;i<this.boards.length;i++){
          if(this.boards[i].groupNo==this.nowboard.groupNo){
            this.temp_now_count++;
          }
        }
      if(this.nowboard > 1){
        alert("서브 게시판이 있어 삭제가 안됩니다.");
        return;
      }
      axios.delete('/blog/myboard/CUD?boardNo='+ this.nowboard.boardNo + '&blogNo=' +  this.searchInfo.blogNo)
      .then(()=>{
        alert("삭제되었습니다.");
        this.$router.go();
      })
    },
    up_board() {
      if(this.nowboard.depth==-1){
        alert("선택이 안됨");
        return;
      }
      if(!(this.beforeboard.depth > 0)){
        alert("위로 이동 할 수 없습니다.1");
        return;
      }
      if(this.beforeboard.depth==1 && this.beforeboard.seqNo==0){
        alert("위로 이동 할 수 없습니다.2");
        return;
      }
      if(this.boards[this.current_index-1].seqNo!=0){ //이것만하자! 거의 완성
        axios.put('/blog/myboard/CUD?boardNo=' + this.nowboard.boardNo +'&seqNo=' + this.beforeboard.seqNo  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.nowboard.boardName + '&boardType=' + this.nowboard.boardType + '&depth=' + this.nowboard.depth + '&groupNo=' + this.nowboard.groupNo) // 현재값을 윗 값으로
        axios.put('/blog/myboard/CUD?boardNo=' + this.beforeboard.boardNo +'&seqNo=' + this.nowboard.seqNo  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.beforeboard.boardName + '&boardType=' + this.beforeboard.boardType + '&depth=' + this.beforeboard.depth + '&groupNo=' + this.beforeboard.groupNo) // 위값을 현재 값으로
        alert("쫄병 2번이상");
      }else if(this.nowboard.seqNo==0){ 
        //이전 데이터의 그룹,졸병여부
        axios.put('/blog/updateRootBoard?boardNo=' + this.nowboard.boardNo +'&blogNo=' + this.searchInfo.blogNo + '&depth=' + this.beforeboard.depth)
        console.log(this.beforeboard.depth+"이전뎁스");
        axios.put('/blog/updateRootBoard?boardNo=' + this.beforeboard.boardNo +'&blogNo=' + this.searchInfo.blogNo + '&depth=' + this.nowboard.depth)
        console.log(this.nowboard.depth+"현재뎁스");
        alert("대빵 위 서브");
      }else { //미완성
        this.beforeboard = this.boards[this.current_index-2];
        axios.put('/blog/myboard/CUD?boardNo=' + this.nowboard.boardNo +'&seqNo=' + (this.beforeboard.seqNo+1)  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.nowboard.boardName + '&boardType=' + this.nowboard.boardType + '&depth=' + (this.nowboard.depth-1) + '&groupNo=' + this.beforeboard.groupNo) // 현재값을 아래그룹으로 편입
        alert("쫄병 1번");
      }
      this.$router.go();
    },
    down_board() {
      console.log("들어옴" + this.heads.length);
      if(this.nowboard.depth==-1){
          alert("선택이 안됨");
          return;
      }
      if(!(this.afterboard.depth > 0)){
        alert("아래로 이동 할 수 없습니다.1");
        return;
      }
      if(this.nowboard.seqNo == 0 && this.nowboard.depth==this.heads.length && this.afterboard==''){
        alert("아래로 이동 할 수 없습니다.2");
        return;
      }
      var temp_now_count = 0;
      if(this.nowboard.seqNo!=0 && this.nowboard.depth!=this.afterboard.depth){//미완성
        temp_now_count = 0;
        for(let i=0;i<this.boards.length;i++){
          if(this.boards[i].depth==this.afterboard.depth){
            temp_now_count++;
          }
        }
        this.afterboard = this.boards[this.current_index+temp_now_count];
        axios.put('/blog/myboard/CUD?boardNo=' + this.nowboard.boardNo +'&seqNo=' + (this.afterboard.seqNo+1)  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.nowboard.boardName + '&boardType=' + this.nowboard.boardType + '&depth=' + this.afterboard.depth + '&groupNo=' + this.afterboard.groupNo) // 현재값을 아래그룹으로 편입
        alert("쫄병 마지막");
      }else if(this.nowboard.depth==this.afterboard.depth && this.nowboard.seqNo!=0){ 
        axios.put('/blog/myboard/CUD?boardNo=' + this.nowboard.boardNo +'&seqNo=' + this.afterboard.seqNo  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.nowboard.boardName + '&boardType=' + this.nowboard.boardType + '&depth=' + this.nowboard.depth + '&groupNo=' + this.nowboard.groupNo) // 현재값을 아래 값으로
        axios.put('/blog/myboard/CUD?boardNo=' + this.afterboard.boardNo +'&seqNo=' + this.nowboard.seqNo  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.afterboard.boardName + '&boardType=' + this.afterboard.boardType + '&depth=' + this.afterboard.depth + '&groupNo=' + this.afterboard.groupNo) // 아래값을 현재 값으로
        alert("쫄병 마지막x");
      }else {
        temp_now_count = 0;
        for(let i=0;i<this.boards.length;i++){
          if(this.boards[i].depth==this.nowboard.depth){
            temp_now_count++;
          }
        }
        this.afterboard = this.boards[this.current_index+temp_now_count];
        // console.log(this.afterboard.boardName);
        axios.put('/blog/updateRootBoard?boardNo=' + this.nowboard.boardNo +'&blogNo=' + this.searchInfo.blogNo + '&depth=' + (this.afterboard.depth))
        axios.put('/blog/updateRootBoard?boardNo=' + this.afterboard.boardNo +'&blogNo=' + this.searchInfo.blogNo + '&depth=' + (this.nowboard.depth))
        alert("대빵");
      }
      this.$router.go();
    },
    left_board() {
      if(this.nowboard.seqNo==0){
        alert("이동 할 수 없습니다.헤드");
        return ;
      }
      this.afterboard = this.boards[this.boards.length-1];

      axios.put('/blog/myboard/CUD?boardNo=' + this.nowboard.boardNo +'&seqNo=' + 0  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.nowboard.boardName + '&boardType=' + this.nowboard.boardType + '&depth=' + (this.afterboard.depth+1) + '&groupNo=' + this.nowboard.boardNo)
      alert("평범한 자료들");
      this.$router.go();
      return ;
    },
    right_board() {
      if(this.nowboard.seqNo>0){
        alert("이동 할 수 없습니다.서브");
        return ;
      }
      if(this.nowboard.seqNo==0 && this.afterboard.seqNo>0){
        alert("이동 할 수 없습니다.서브게시판존재");
        return ;
      }
      axios.put('/blog/myboard/CUD?boardNo=' + this.nowboard.boardNo +'&seqNo=' + (this.beforeboard.seqNo+1)  +'&blogNo=' + this.searchInfo.blogNo +'&boardName=' + this.nowboard.boardName + '&boardType=' + this.nowboard.boardType + '&depth=' + this.beforeboard.depth + '&groupNo=' + this.beforeboard.groupNo) // 메인을 서브로
      alert("가즈아");
      this.$router.go();
      return ;
    },
  }
};
</script>

