<template>
  <div class="friends container col order">
    <div class="row py-3">
      <!-- data가 들어갈 부분 -->
      <div class="col" id="main">
        <v-container fluid>
          <v-row align="center">
            <v-col cols="2">
              <v-text-field v-model="search" clearable flat solo-inverted hide-details label="Search" @keyup.enter="search_friends"></v-text-field>
            </v-col>
            <b-button @click="search_friends">검색</b-button>
          </v-row>
        </v-container>

        <!-- 탭 -->
        <b-tabs content-class="mt-3">
          <!-- 맞팔로우 -->
          <b-tab title="맞팔로우" active @click="tab_one"> 
            <v-card max-width="1100" class="mx-auto">
              <v-list-item v-for="user in follow" :key="user.number" two-line>    
                <v-list-item-content>         
                  <v-list-item-title class="my-1">{{user.avatar}} {{user.nickName}}({{user.userId}})</v-list-item-title>
                  <v-list-item-subtitle>Blog.{{user.blogName}}</v-list-item-subtitle>
                </v-list-item-content>
                <li><button @click="follow_delete(user.following)">끊기</button></li>
                <li><button @click="block_follow(user.following)">차단</button></li>
              </v-list-item>
            </v-card>
          </b-tab>
          <!-- 팔로잉 -->
          <b-tab title="팔로잉" @click="tab_two">
            <v-card max-width="1100" class="mx-auto">
              <v-list-item v-for="user in following" :key="user.number" two-line>
                <v-list-item-content> 
                  <v-list-item-title class="my-1">{{user.avatar}} {{user.nickName}}({{user.userId}})</v-list-item-title>
                  <v-list-item-subtitle>Blog.{{user.blogName}}</v-list-item-subtitle>
                </v-list-item-content>
                <li><button @click="follow_delete(user.following)">끊기</button></li>
                <li><button @click="block_follow(user.following)">차단</button></li>
              </v-list-item>
            </v-card>
          </b-tab>
          <!-- 팔로워 -->
          <b-tab title="팔로워" @click="tab_three">
            <v-card max-width="1100" class="mx-auto">
              <v-list-item v-for="user in follower" :key="user.number" two-line>
                <v-list-item-content>
                  <v-list-item-title class="my-1">{{user.avatar}} {{user.nickName}}({{user.userId}})</v-list-item-title>
                  <v-list-item-subtitle>Blog.{{user.blogName}}</v-list-item-subtitle>
                </v-list-item-content>
                  <li><button @click="follow_you(user.follower)"> 팔로우</button></li>
                  <li><button @click="block_follow(user.follower)"> 차단</button></li>
              </v-list-item>
            </v-card>
          </b-tab>
        </b-tabs>

        <!-- 페이지네이션 -->
        <v-col cols="12">
          <v-container id="page" class="max-width">
            <v-pagination
              v-model="page"
              :circle="circle"
              :disabled="disabled"
              :length="10"
              :next-icon="nextIcon"
              :prev-icon="prevIcon"
              :page="page"
              :total-visible="totalVisible"
            ></v-pagination>
          </v-container>
        </v-col>
        
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "friends",
  components: {},
  data() {
    return {
      rows: 100,
      follower : [],
      following : [],
      follow : [],
      search : '',
      select : 1,
      page: 1,
    };
  },
  created(){
    axios.get('/user/following')
    .then((Response)=>{
      console.log(Response);
      this.following = Response.data;
    }),
    axios.get('/user/follower')
    .then((Response)=>{
      console.log(Response);
      this.follower = Response.data;
    }),
    axios.get('/user/f4f')
    .then((Response)=>{
      console.log(Response);
      this.follow = Response.data;
    })
  },
  methods: {
    tab_one() {
      this.select = 1;
      axios.get('/user/f4f')
    .then((Response)=>{
      console.log(Response);
      this.follow = Response.data;
    })
    },
    tab_two() {
      this.select = 2;
      axios.get('/user/following')
    .then((Response)=>{
      console.log(Response);
      this.following = Response.data;
    })
    },
    tab_three() {
      this.select = 3;
      axios.get('/user/follower')
    .then((Response)=>{
      console.log(Response);
      this.follower = Response.data;
    })
    },
    search_friends() {
      if(this.select==1){
        axios.get('/user/f4f?searchValue='+encodeURI(this.search))
        .then((Response)=>{
        console.log(Response);
        this.follow = Response.data;
    })
      }else if(this.select==2){
          axios.get('/user/following?searchValue='+encodeURI(this.search))
          .then((Response)=>{
          console.log(Response);
          this.following = Response.data;
          })
          
      }else{
        axios.get('/user/follower?searchValue='+encodeURI(this.search))
        .then((Response)=>{
        console.log(Response);
        this.follower = Response.data;
    })
      }
    },
    follow_delete(number) {
      console.log(number)
      axios.delete(`/user/follow/`+ number).then((Response)=>{
        console.log(Response);
        alert("팔로우가 삭제되었습니다.");
        this.$router.go();
      })
    },
    follow_you(number){
      axios.post(`/user/follow/`+ number).then((Response)=>{
        console.log(Response);
        alert("팔로우가 되었습니다.");
        this.$router.go();
      })
    },
    block_follow(number){
      axios.post(`/user/block/` + number).then((Res)=>{
          console.log(Res);
          alert("차단 되었습니다.");
          this.$router.go();
        })
    }
  }
};
</script>

