<template>
  <div>
    <nav-header></nav-header>

    <!-- 회원가입 -->
    <v-container class="fill-height" fluid>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="8" md="4">
          <v-card class="elevation-12">
            <v-toolbar color="primary" dark flat>
              <v-toolbar-title>Sign Up</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <v-form>
                <v-text-field 
                  label="아이디" 
                  name="아이디"
                  hint="6~16 영문, 숫자" 
                  type="text"
                  v-model="userId"
                  maxlength="12"
                  onkeyup="this.value=this.value.replace(/[^a-zA-Z0-9]/g,'');"
                  autofocus
                ></v-text-field>
                <button type="button" @click="idcheck">중복확인</button>

                <v-text-field
                  id="password"
                  label="비밀번호"
                  name="비밀번호"
                  hint="최대 12자" 
                  type="password"
                  v-model="userPw"
                  maxlength="12"
                ></v-text-field>

                <v-text-field
                  id="password"
                  label="비밀번호 확인"
                  name="비밀번호 확인"
                  hint="비밀번호 재입력" 
                  type="password"
                  v-model="userPwcheck"
                ></v-text-field>

                <v-text-field
                  label="이름"
                  type="text"
                  v-model="userName"
                  maxlength="12"
                ></v-text-field>

                <v-text-field
                  label="닉네임"
                  type="text"
                  v-model="nickName"
                  maxlength="12"
                ></v-text-field>

                <v-text-field
                  label="이메일 주소"
                  type="text"
                  v-model="eMail"
                  maxlength="30"
                ></v-text-field>

                <v-text-field
                  label="연락처"
                  type="text"
                  v-model="phoneNum"
                  maxlength="15"
                ></v-text-field>

              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" @click="onSubmit">Sign Up</v-btn>
              <v-btn color="primary" @click="home">Back</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>

    <nav-footer></nav-footer>
  </div>
</template>

<script>
// import {mapActions,mapGetters,mapMutations} from 'vuex';
import NavHeader from "@/views/Main/NavHeader.vue";
import NavFooter from "@/views/Main/NavFooter.vue";
import axios from "axios";
export default {
  name: "Join",
  data() {
    return {
      userdata: {
        userId: "",
        userIdcheck: "",
        userPw: "",
        userPwcheck: "",
        userName: "",
        nickName: "",
        eMail: "",
        phoneNum: "",
        idcheckflag: false
      }
    };
  },
  methods: {
    onSubmit() {
      if (this.userPw == "" || this.userPwcheck == "") {
        alert("비밀번호를 입력 해주세요");
      } else if (this.userPw != this.userPwcheck) {
        alert("비밀번호를 다시 확인 해주세요");
      } else {
        this.joincomplete();
      }
    },
    idcheck() {
      axios
        .post("/user/idcheck", {
          userId: this.userId
        })
        .then(response => {
          if (response.data) {
            this.idcheckflag = false;
            alert("중복된 아이디입니다.");
          } else {
            this.idcheckflag = true;
            this.userIdcheck = this.userId;
            alert("사용가능한 아이디입니다.");
          }
        });
    },
    joincomplete() {
      if (this.idcheckflag && this.userIdcheck == this.userId) {
        axios
          .post("/user/join", {
            userId: this.userId,
            userPw: this.userPw,
            userName: this.userName,
            nickName: this.nickName,
            eMail: this.eMail,
            phoneNum: this.phoneNum
          })
          .then(response => {
            console.log(response);
            alert("회원 가입이 완료되었습니다.");
            this.$router.push("/");
          })
          .catch(error => {
            console.log(error);
            alert("오류 발생!");
            this.$router.push("/");
          });
      } else {
        alert("id가 중복이거나 확인이 되지 않았습니다.");
      }
    },
    home() {
      this.$router.push("/");
    }
  },
  components: {
    NavHeader,
    NavFooter
  }
};
</script>

<style scoped>
.join {
  text-align: left;
}
</style>
