<template>  
  <div class="accounts container col order">
    <div class="row py-3">
      <!-- data가 들어갈 부분 -->
      <div class="col" id="main">
        <v-col cols="11" id="accounts">
          <div id="left">
            <h4><b>프로필 수정</b></h4>
            <p>SSAZIT 대표 프로필과 별명을 수정하실 수 있습니다.</p>
          </div>
          <!-- 프로필 이미지 -->
          <div id="left">
            <hr>
            <div>
              <img class="profile-image" :src="userImage" width="170px" height="170px" />
            </div>
            <div :class="{ active: isActive }"> 
              <input type="file" round class="change-profile-image" @change="onFileChange" ref="fileupload"/> 
              <button v-on:click="imgUpdateSubmit()">수정</button>&nbsp;
              <button v-on:click="imgUpdateCancel()">취소</button>
            </div>
            <div :class="{ active: !isActive }">
              <button v-on:click="toggle">수정</button>
              <button>삭제</button>
            </div>
          </div>
          <!-- 계급 이미지, 닉네임 -->
          <div id="right">
            <li><router-link to="/rank"><img id="level" :src="ssazit"/></router-link></li>
            <li><h4>닉네임 : {{ userInfo.nickName }}</h4></li>
            <button>변경</button>
            <!-- 경험치 애니메이션 -->
            <div class="container">    
              <div class="progress progress-striped">
                <div class="progress-bar">
                </div>                       
              </div> 
            <p>{{userInfo.userExp}}/10000</p>
            </div>
          </div>
        </v-col>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
  name : "accounts",
  components: {
      
  },
  created() {
    axios.get('/user/mypage').then((Response)=>{
      this.$store.dispatch('Re_userInfo',Response.data)
      console.log(Response.data);
      console.log(Response.data.profileImg);
      console.log("1");
      console.log(this.userImage);
      console.log(this.serverURL);
      this.userImage = this.serverURL + Response.data.profileImg;
    })

    // getUserInfo() {
    //   axios.get('/user/mypage').then((Response)=>{
    //     this.$store.dispatch('Re_userInfo',Response.data)
    //     console.log(Response.data);
    //     // this.userImage = 'http://192.168.1.6:9999/' + Response.data.userInfo.profileImg;
    //   }).catch(function(){
    //     console.log('FAILURE!!');
    //   })
    // },
  },
  computed:{
    userInfo(){
      return this.$store.getters.userInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  data(){
    return{
      ssazit : require('../images/ssazit.jpg'),
      file: '',
      userImage: '',
      isActive: true
    }
  },
  methods: {
    onFileChange(e) { 
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) { 
        return;
      } 
      console.log("filechange!");
      console.log(this.userInfo);
      this.file = files[0];
      console.log(files[0]);
      this.createImage(files[0]);
    }, 
    createImage(file) { 
      var reader = new FileReader();
      var vm = this;
      reader.onload = (e) => { 
        vm.userImage = e.target.result;
      } 
      console.log("createImage!!");
      reader.readAsDataURL(file);
    },
    removeImage: function (e) {   // eslint-disable-line no-unused-vars
      console.log("removeImage");
      this.file = '';
      this.userImage = this.serverURL + this.userInfo.profileImg;
      console.log("removeImage : done");
    }, 

    sendImage() {
      console.log("sendImage");
      let form = new FormData();
      form.append("file",this.file);
      axios.post(`/user/upload`, form
      )
      .then((Response)=>{
        console.log("####");
        console.log(Response.data);
        this.$store.dispatch('Re_userInfo',Response.data);
        console.log('sendImage : SUCCESS!!');
      })
      .catch(function(){
        console.log('sendImage :FAILURE!!');
      });
    },
    ckSendImage(){
        console.log("cksendImage");
        var ck = confirm("Are you sure?");
        if(ck){
          this.sendImage();
          this.toggle();
          console.log("cksendImage : yes!");
        }
        else{
          console.log("cksendImage : cancel");
        }
    },
    toggle: function () {
      this.isActive = !this.isActive;
      this.$refs.fileupload.value=null;
    },
    imgUpdateSubmit(){
      console.log("적용");
      this.ckSendImage();
    },
    imgUpdateCancel(){
      console.log("초기화");
      this.removeImage();
      console.log(this.userInfo.profileImg);
      this.toggle();
    }

  } 
}
</script>

<style>
li {
  display: inline-block;
  margin: 0 10px;
}
#accounts {
  text-align: left;
}
#level {
  width: 100px;
  height: 80px;
}
#left {
  float: left;
  width: 100%;
}
#right {
  float: right;
  width: 100%;
}

.container {
  margin: 100px auto;
  width: 500px;
  text-align: center;
}

.progress {
  padding: 6px;
  background: rgba(0, 0, 0, 0.25);
  border-radius: 6px;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.25), 0 1px rgba(255, 255, 255, 0.08);
}

.progress-bar {	
  height: 18px;
	background-color: #ee303c;  
  border-radius: 4px; 
  transition: 0.4s linear;  
  transition-property: width, background-color;    
}

.progress-striped .progress-bar { 	
  background-color: #FCBC51; 
  width: 100%; 
  background-image: linear-gradient(
    45deg, rgb(252,163,17) 25%, 
    transparent 25%, transparent 50%, 
    rgb(252,163,17) 50%, rgb(252,163,17) 75%,
    transparent 75%, transparent); 
  animation: progressAnimationStrike 6s;
}

.active {
  display : none;
}


@keyframes progressAnimationStrike {
  from { width: 0 }
  to   { width: 100% }
}
</style>